.nav {
  font-family: "Bebas Neue", sans-serif;
  margin: 59px 30px 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.items {
  margin-left: -15px;
  margin-right: 31px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.item {
  font-size: 24px;
  color: #1d1d1b;
  text-align: right;
  text-decoration: none;
  text-transform: uppercase;
  transform: scale(0.57, 1);
}
.price {
  letter-spacing: 0.9px;
  margin-right: -10px;
}
.phone {
  margin-left: 41px;
  margin-right: -30px;
  width: 171px;
  height: 41px;
  border-radius: 10px;
  background-color: transparent;
  border: 1px solid #c7af8a;
  text-transform: uppercase;
  transform: scale(0.9999, 1);
  letter-spacing: 0.099px;
}
.phone a {
  font-size: 24px;
  font-weight: 100 !important;
  color: #1d1d1b;
  text-decoration: none;
}
