.wrapper {
  width: 1390px;
  margin-top: -191px;
  height: 150px;
  float: right;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-image: url(../../../assets/image/booking_back.png);
}
.title {
	margin: 0px -83px 0 -51px;
  padding-top: 3px;
  font-size: 1.875rem;
  font-weight: 400;
  text-align: right;
  text-transform: uppercase;
  transform: scale(0.57, 1);
  color: #1d1d1b;
}
.textField {
	width: 161px;
	height: 48px;
		display: flex;
		align-items: center;
		justify-content: space-around;
		background-color: #ffffff;
		border-radius: 3px;
}
.textField_image {
	padding-top: 7px;
	padding-left: 8px;
}
.textField_content {
	display: flex;
	padding-right: 9px;
  display: flex;
  flex-direction: column;
    justify-content: center;
}
input[type="date"]::-webkit-calendar-picker-indicator {
	margin-top: -35px;
  margin: 0;
  width: 20px;
  height: 20px;
  display: flex;
  opacity: 1;
  background: url(../../../assets/image/small-calendar.svg) no-repeat;
  color: rgba(0, 0, 0, 0);
}
.test-label {
  padding-left: 37px;
  padding-top: 9px;
}
input {
